body > #root > div > div > #menuBody {
    height: 100vh;
}

#menuBody{
    display: flex;
    align-items: center;
    justify-content: center;
}

body > #root > div > div > #menuBody > #menu {
    height: 100%;
    width: 100%;
}

#menu{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color:rgb(191, 203, 214);
    /* background-color: black; */
}

#menuBar{
    height: 100%;
    width: 25%;
    margin: 0;
    padding: 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

#barIcons{
    height: 10%;
    display: flex;
    justify-content: space-around;
}

#barIcons .button{  
    display: inline-block;
    background-color: transparent;
    margin-right: 0;
    margin-left: 0;
    padding-right: 0;
    padding-left: 0;
    /* background-color: rgb(49, 49, 49); */
}

#barIcons .button:hover i{  
    color:rgba(98, 123, 236, 0.9);
}

#menuDivider{
    display: inline-block;
    height: 100%;
    justify-content: initial;
}

#menuDivider .button{
    background-color: transparent;
}

#menuDividerMobile{
    position: absolute;
    bottom: 20px;
    right: 20px;
    z-index: 1;
}

#menuDividerMobile .button{
    border-radius: 25px;
    background-color: lightblue;
}

/* #barIcons .icon{
    cursor: pointer;
} */

#barBooks{
    /* margin-top: 10%; */
    height: 90%;
}

#content{
    height: 100%;
    width: 85%;
    margin: 0;
}

#contentMobile{
    height: 100%;
    width: 100%;
    margin: 0;
}



