#bookHeader{
    display: flex;
    /* flex-direction: ; */
}

#headerText{
    /* margin-left: 2%; */
    display: flex;
    flex-direction: column;
}

#headerText p{
    padding: 2%;
    text-align: justify;
    border-radius: 15px;
    cursor: pointer;
}  

#headerText p:hover{
    background-color: rgba(247, 243, 243, 0.89);
    border: gray 1px solid;
}

#headerText h1{
    margin: 0;
    padding: 0;
}

#headerText h3{
    margin-top: 0;
    margin-bottom: 0;
    padding-top: 0;
}

#quoteList{
    height: 60%;
    margin-top: 5%;
}

#imageEdit{
    cursor: pointer;
}

#imageEdit:hover{
    opacity: .7;
}

#emptySubscription{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}